.user-card-top {
  background-color: white;
  min-width: 600px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 8px;
  padding-bottom: 8px;
  -webkit-box-shadow: 0 3px 9px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 3px 9px 0 rgba(0, 0, 0, 0.2);
}

.user-card-avatar-image {
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

.user-card-info {
  padding-left: 16px;
}

.user-card-user-name {
  font-size: 24px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.user-card-profile-url {
  padding-bottom: 8px;
}

.user-card-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.user-card {
  margin-top: 16px;
}

.user-expanded-card {
  background-color: white;
  -webkit-box-shadow: 0 3px 9px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 3px 9px 0 rgba(0, 0, 0, 0.2);
}



