.user-repo-state {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 16px;
  padding-top: 16px;
  padding-bottom: 16px;
}

.user-repo-error {
  color: red;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table,
td,
th {
  border: 1px solid #ddd;
  text-align: center;
}

table tr:nth-child(odd) {
  background-color: #f2f2f2;
}

th,
td {
  padding: 15px;
}



